
import { defineComponent, ref, computed, watch } from 'vue'
import {
  IonButton,
  IonCard,
  IonIcon,
  IonCardHeader,
  IonCardContent,
  IonCardTitle,
  IonCardSubtitle
} from "@ionic/vue"
import {
  warning
} from "ionicons/icons"
import {
  isOnline,
  isOfflineMode
} from "@/composables/offline"
import { alertConfirmation, toastSuccess } from '@/utils/Alerts'
import { AuthService } from '@/services/auth_service';

export default defineComponent({
  components: {
    IonButton,
    IonCard,
    IonIcon,
    IonCardHeader,
    IonCardContent,
    IonCardTitle,
    IonCardSubtitle
  },
  setup() {
    const auth = new AuthService()
    const isLoggedIn = ref(false)
    const refresh = () => location.reload()
    const online = computed(() => isOnline.value)
    const switchOffline = () => {
      alertConfirmation("This mode provides limited features of the application, are you sure you want to activate?")
        .then((ok: boolean) => {
          if (ok) {
            isOfflineMode.value=true
            toastSuccess("Offline mode activated")
          }
        })
    }
    watch(isOnline, () => isLoggedIn.value = auth.isLoggedIn(), { immediate: true })
    return {
      warning,
      online,
      isLoggedIn,
      isOfflineMode,
      switchOffline,
      refresh
    }
  }
})
